import popupLayer from '@/components/sl-filter/popup-layer.vue';
import slFilterView from '@/components/sl-filter/filter-view.vue';
import msDropdownMenu from '@/components/ms-dropdown/dropdown-menu.vue';
import msDropdownItem from '@/components/ms-dropdown/dropdown-item.vue';
import searchSelect from '@/components/search-select/sl-filter.vue';
import Constant from '../../util/constant.js'

export default {
	components: {
		popupLayer,
		slFilterView,
		msDropdownMenu,
		msDropdownItem,
		searchSelect
	},
	props: {
		menuList: {
			type: Array
		},
		themeColor: {
			type: String,
			default: "#000"
		},
		color: {
			type: String,
			default:"#666"
		},
		independence: {
			type: Boolean,
			default: false
		},
		isTransNav: {
			type: Boolean,
			default: false
		},
		navHeight: {
			type: Number,
			default: 0
		},
		topFixed: {
			type: Boolean,
			default: false
		},
		//选中信息（细项数据）
		selectList: {
			type: Object
		},
		selectSort:{
			type: Object
		}
	},

	data() {
		return {
			// down: 'sl-down',
			// up: 'sl-up',
			tabHeight: 40,
			// statusList: [],
			// selectedIndex: '',
			//单个选项标题组
			subKey:Constant.searchKeys[0],
			
			// tempTitleObj: {},
			// state: '',
			// stateNum: 1,
			// hFlag: true,
			// hNum: 1,
			
			//排序
			sortList: Constant.items,
			// sortIndex: "0",
			
			
			selectData: {},
			houseTypeId: null,
			areaId: null,
			styleId: null,
			workedId: null,
			budgetId: null,
			movesFlag: ''
		};
	},
	
	computed: {
		/**
		 * 监听小标题的变化效果
		 */
		computedSub(){
			this.setItemId(this.selectList);
			this.selectSortItem(this.selectSort);
		}
	},

	mounted() {
		
	},
	
	
	onReady (){
		// console.log(this.selectList);
		//判断是否为空
		if(Object.keys(this.selectList).length > 0){
			this.selectData = this.selectList;
		}else{
			this.reset();	
		}
		//初始化细项的title
		// this.setSubTitle();
		
		
	},
	
	methods: {
		/**
		 * title导航设置
		 */
		// setSubTitle(){
		// 	if(this.menuList && this.menuList.length > 0){
		// 		this.subTitle = [];
		// 		this.menuList.forEach(menu =>{
		// 			this.subTitle.push({
		// 				showPopup:false,
		// 				title: menu.title,
		// 				value: menu.value,
		// 				key: menu.key
		// 			})
		// 		})
		// 	}
		// },
		
		/**
		 * 单击选择下拉项
		 * @param {Object} item
		 */
		subMenu(item){
			let showPopup = false;
			this.subKey = item.key;
			for(let menu of this.menuList){
				if(menu.key === item.key){
					item.showPopup = !item.showPopup;
					showPopup = item.showPopup;
				}
			}
			this.$refs.itemFilterView.resetItemId();
			if(showPopup){
				this.$refs.popupRef.show();	
			}else{
				this.$refs.popupRef.close();
			}
			
		},
		
		
		/**
		 * 选中的排序规则
		 * @param {Object} item
		 */
		doSort(item){
			this.$emit("sortResult",item);
			this.$refs.sortRef.close();
		},
		
		
		
		
		moveFlag(e) {
			this.$emit('moveFlag', e)
		},
		
		/**
		 * 单击选项后回调
		 * @param {Object} item
		 */
		confirmItem(item) {
			console.log(this.subKey)
			this.selectData[this.subKey] = item;
			if(this.subTitle && this.subTitle.length > 0){
				this.subTitle.map(sub =>{
					sub.showPopup = false;
				})
			}
			this.$emit("selectItem",this.selectData);
			this.close();
		},

		/**
		 * 关闭弹出框
		 */
		close() {
			this.$refs.popupRef.close();
		},

		/**
		 * 点击单个
		 * @param {Object} item
		 * @param {Object} key
		 */
		selectItem(item, key) {
			this.selectData[key] = item;
			this.setItemId(this.selectData);
		},
		
		/**
		 * 选中的sort
		 * @param {Object} item
		 */
		selectSortItem(item){
			if(this.sortList && this.sortList.length > 0 && item["content"]){
				this.sortList.forEach(sort =>{
					sort.actived = sort.value === item.value;
				})
			}
			// console.log(this.sortList);
		},
		
		/**
		 * 全部筛选时的确认
		 */
		confirm() {
			this.$emit("screen", this.selectData);
			this.$refs.allRef.closePopup();
		},
		
		/**
		 * 全部筛选时的重置
		 */
		reset() {
			if (this.menuList && this.menuList.length > 0) {
				this.menuList.forEach(menu => {
					this.selectData[menu.key] = menu.detailList[0];
				})
			}
			// this.selectList = this.selectData;
			this.$emit("screen", this.selectData);	
			this.$refs.allRef.closePopup();
		},

		/**
		 * 重置单项中的id
		 * @param {Object} data 选中的data
		 */
		setItemId(data) {
			for (let key in data) {
				switch (key) {
					case Constant.searchKeys[0]:
						this.houseTypeId = data[key].id;
						break;
					case Constant.searchKeys[1]:
						this.budgetId = data[key].id;
						break;
					case Constant.searchKeys[2]:
						this.areaId = data[key].id;
						break;
					case Constant.searchKeys[3]:
						this.styleId = data[key].id;
						break;
					case Constant.searchKeys[4]:
						this.workedId = data[key].id;
						break;
					default: break;
				}
			}
		}

	}
}
